*{font-family:'Indie Flower',cursive;font-size:20px;box-sizing:border-box}body{margin:0;width:100%}#root{height:100vh;overflow-x:hidden}.app{width:100vw;max-width:1280px;margin:0 auto;padding:30px 0;background:linear-gradient(-10deg, #0051ffe8, #0546d2d4, #0931a1)}.app>.find{width:-webkit-fit-content;width:fit-content;margin-left:30px}.app>.find>input{border-radius:10px;margin-right:5px;padding-left:10px}.app>.find>button{background-color:#00caff;border:none;border-radius:10px}.day{width:100px;padding-top:10px;display:flex;flex-direction:column;border-radius:10px;background-color:#0000004d;color:#ffffffad;text-align:center}.current{margin-bottom:30px;display:flex;justify-content:center}.current__info{height:300px;width:30%;min-width:250px;max-width:350px}.future{margin-top:-10px;display:flex;justify-content:center;flex-wrap:wrap}.future__info{margin-top:10px;margin-right:10px;width:20%;max-width:300px;min-width:250px;padding:2px}.temp__current,.temp__data{color:white}.temp__data,.temp__feels{list-style-type:none;padding:0;display:flex;margin:5px 0}.temp__feels{justify-content:space-evenly}.location{display:flex;align-items:center;justify-content:center}.loading{background:linear-gradient(-10deg, #0051ffe8, #0546d2d4, #0931a1);height:100vh;display:flex;align-items:center;justify-content:center}.loading>span{color:#f5f5f5d1;font-size:50px}@media (max-width:900px){.app>.find{margin:0 auto 30px}}@media (max-width:850px){.current__info{background-color:#21fc754d;color:#000000d4}}